<template>
    <div v-loading="loading">
        <el-form label-width="100px" size="mini">
            <el-form-item label="所有快递：">
                <el-checkbox  :value="getIsAll()"  @change="setAll">全选</el-checkbox>
            </el-form-item>
            <el-form-item v-for="(item,index) in area" :label="item.shipping_name + '：'">
                <el-checkbox :value="getIsSelectAll(index)" @change="setItemAll(index)">全选</el-checkbox>
                <el-checkbox-group v-model="areaIds">
                    <el-checkbox v-for="(v,k) in item.area" :key="v.shipping_area_id" @change="setCallBack"
                                 :label="v.shipping_area_id">{{v.shipping_area_name}}
                    </el-checkbox>
                </el-checkbox-group>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import * as api from "@/api";

    export default {
        name: "SelectShipping",
        props: ['shippingAreaIds'],
        data() {
            return {
                area: [],
                areaIds: [],
                loading: false,
            }
        },
        created() {
            this.getShippongArea();
        },
        methods: {
            setCallBack() {
                this.$emit("input", this.areaIds)
            },
            setItemAll(index) {
                let area = this.area[index].area;
                let val = this.getIsSelectAll(index);
                if (!val) {
                    area.forEach(v => {
                        if (this.areaIds.indexOf(v.shipping_area_id) <= -1) {
                            this.areaIds.push(v.shipping_area_id);
                        }
                    })
                } else {
                    area.forEach(v => {
                        if (this.areaIds.indexOf(v.shipping_area_id) <= -1) {
                            this.areaIds.splice(v.shipping_area_id, 1);
                        }
                    })
                }
                this.setCallBack();
            },
            setAll(val) {
                let areaIds = [];
                if (val == true) {
                    this.area.forEach((val, key) => {
                        val.area.forEach(v => {
                            areaIds.push(v.shipping_area_id);
                        })
                    });
                }
                this.areaIds = areaIds;
                this.setCallBack();
            },
            getIsSelectAll(index) {
                let area = this.area[index].area;
                let isAll = true;
                area.forEach(v => {
                    if (this.areaIds.indexOf(v.shipping_area_id) <= -1) {
                        isAll = false;
                    }
                })
                return isAll;
            },
            getShippongArea() {
                this.loading = true;
                api.post("/shop/admin.shipping/getDetail", {
                    appToken: this.$store.getters.appToken,
                }).then(res => {
                    this.loading = false;
                    this.area = res.list;
                    this.areaIds = this.shippingAreaIds ? this.shippingAreaIds : []
                }).catch((error) => {
                    this.loading = false;
                });
            },
            getIsAll() {
                let all = true;
                this.area.forEach((val, key) => {
                    val.area.forEach(v => {
                        if(this.areaIds.indexOf(v.shipping_area_id) <= -1){
                            all = false;
                        }
                    })
                });
                return all;
            }
        },
    }
</script>

